import { Component, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { BigimageComponent } from '../bigimage/bigimage.component';
import { SmallimageComponent } from '../smallimage/smallimage.component';
import { DetailComponent } from '../detail/detail.component';
import { AboutComponent } from '../about/about.component';
import { iframe } from '../iframe/iframe.component';
import { AccuweatherComponent } from '../accuweather/accuweather.component';

const routes: Routes = [
  { path: '', redirectTo: 'smallimage/budapest', pathMatch: 'full' },
  { path: '*', redirectTo: 'smallimage/paris', pathMatch: 'full' },
  { path: 'about', component: AboutComponent },
  { path: 'smallimage/larnach', component: SmallimageComponent },
  { path: 'smallimage/paris', component: SmallimageComponent },
  { path: 'smallimage/spain', component: SmallimageComponent },
  { path: 'smallimage/budapest', component: SmallimageComponent },
  { path: 'accuweather', component: AccuweatherComponent },
  { path: 'iframeComponent/isabel', component: iframe },
  { path: 'iframeComponent/ClassicCinema', component: iframe },
  { path: 'detail/:i', component: DetailComponent },
  { path: '**', component: AboutComponent },
];
@NgModule({
  imports: [RouterModule.forRoot(routes), CommonModule],
  exports: [RouterModule],
  declarations: [],
})
export class AppRoutingModule {}
