import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing/app-routing.module';
import { BigimageComponent } from './bigimage/bigimage.component';
import { SmallimageComponent } from './smallimage/smallimage.component';
import { DetailComponent } from './detail/detail.component';
import { AboutComponent } from './about/about.component';
import { AccuweatherComponent } from './accuweather/accuweather.component';
import { AccuserviceService } from './accuservice.service';
import { HttpClientModule } from '@angular/common/http';
import { AccucityService } from './accucity.service';
import { ReactiveFormsModule } from '@angular/forms';


@NgModule({
  declarations: [
    AppComponent,
    BigimageComponent,
    SmallimageComponent,
    DetailComponent,
    AboutComponent,
    AccuweatherComponent,
  ],
  imports: [BrowserModule, AppRoutingModule, HttpClientModule, ReactiveFormsModule],
  providers: [AccuserviceService, AccucityService],
  bootstrap: [AppComponent],
})
export class AppModule { }
