import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';


@Injectable({
  providedIn: 'root'
})
export class AccucityService {

  constructor(private http: HttpClient) { }
  getCity(location) {
    return this.http.get(
      'http://dataservice.accuweather.com/locations/v1/cities/search?apikey=BIilFHGmyYwCasU6E1me1RBkj3MNdNfN&q=' + location
    );
  }
}
