import { Component, OnInit } from '@angular/core';
import { AccuserviceService } from '../accuservice.service';
import { AccucityService } from '../accucity.service';
import { FormBuilder, FormGroup, FormControl } from '@angular/forms';

@Component({
  selector: 'app-accuweather',
  templateUrl: './accuweather.component.html',
  styleUrls: ['./accuweather.component.css'],
})
export class AccuweatherComponent implements OnInit {
  public weatherData: any;
  public cityData: any;
  private formBuilder: FormBuilder;
  public citySearchForm: FormGroup;


  constructor(private accuweather: AccuserviceService, private accucity: AccucityService) { }

  ngOnInit(): void {
    //    this.citySearchForm = this.formBuilder.group({ location });
    this.citySearchForm = new FormGroup({
      location: new FormControl()
    });
  }

  sendToCity(formValues) {
    this.accucity.getCity(formValues.location).subscribe((data) => {
      this.cityData = data;
      if (this.cityData != '') {
        this.sendToAPI(this.cityData[0].Key);
        console.log('City code:' + data[0].Key);
        console.log(data);
      } else {
        this.sendToAPI('255042');
        console.log(' Default data : 255042');
      }
    });
  }

  sendToAPI(code) {
    this.accuweather.getWeather(code).subscribe((data) => {
      console.log('Pressed');
      this.weatherData = data;
      console.log(this.weatherData);
    });
  }
}
